/*--------------Color Header------------------*/
.Front {
    --color-header:#95BAF3;
}

/*------------------Skills--------------*/
.skills-text{
    max-width: 50vw;
    margin: 0 auto;
}
.skills__section-title {
    color: var(--color-home);
    display:flex;
    flex-direction: column;
    font-size: 24px;
    text-align: left;
    margin:48px 0;
   
}
.skills__section-description {
    margin:48px 0;
    font-size: 14px;
    text-align: left;
    
}
.front__container-projects {
    margin:0 auto;
}

.front-img-project {
    width: 330px;
    margin-top:136px ;
    margin-bottom:48px ;
}

.container-buttons-front {
    margin: 48px 0;
    
}
   


/*---------------------Medium Break----------------------*/

@media all and (min-width: 650px) {
    .skills-text {
       display: inline-block;
    }
    .skills__section {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: -6vw;
    } 
    .front-img-project {
        width: 600px;
    }
    
}


/*--------------------- BREAK----------------------*/
@media all and (min-width: 1000px){
    .front-img-project {
        width:900px;
    }

    .skills__section-description {
        width: 20vw;  
    }
    .skills__section {
        grid-template-columns: repeat(3, 1fr);
        max-width: 900px;
        margin:0 auto;
    }   

}